import axios from 'axios';

import { DOMAIN, services as baseServices, Service } from '@miq/utils';

export const ENDPOINT = `${DOMAIN}${process.env.REACT_APP_API_ENDPOINT}`;

axios.defaults.xsrfCookieName = 'csrftoken';
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN';
// axios.defaults.headers["Content-Type"] = "text/html; charset=UTF-8";
axios.defaults.headers.post['Content-Type'] = 'application/json';

export const API = axios.create({
  baseURL: ENDPOINT,
  timeout: 3000,
  withCredentials: true,
  // headers: { "X-Requested-With": "XMLHttpRequest" },
  headers: {
    'Content-type': 'application/json',
  },
  // onUploadProgress: (progressEvt) => {},
});

const isArray = Array.isArray;

export const hasPerms = (perms, requiredPerms) => {
  if (!isArray(perms) || !isArray(requiredPerms)) return false;
  return perms.filter((perm) => requiredPerms.includes(perm)).length === requiredPerms.length;
};

export const services = baseServices(API);
