import { useContext } from 'react';

import Form, { useForm } from '@miq/form';
import { services, AdminView } from '@miq/adminjs';
import { SharedDataCtx } from '@miq/contexts';
import { AvatarUploadButton, Avatar } from '@miq/components';
import { ImgDeleteIconButton } from '../exports';

const errorMessages = {
  first_name: 'Enter a valid first name',
  last_name: 'Enter a valid last name',
};

export const AccountProfileUpdateView = (props) => {
  const { user, updateUser, apps } = useContext(SharedDataCtx);
  const { employee } = user;

  const formInitData = { first_name: user.first_name, last_name: user.last_name };
  if (employee) formInitData.position = employee.position;

  const form = useForm(formInitData);

  const saveAccount = ({ name, value }) => {
    services
      .patch(
        'account/current/',
        { [name]: value },
        { [name]: user[name] },
        { headers: { 'content-type': 'application/json' } }
      )
      .then((data) => {
        updateUser(data);
      })
      .catch((err) => {
        form.handleError(err);
      });
  };

  const validate = ({ value, name }) => {
    if (value.length < 3) {
      form.setErrors({ ...form.errors, [name]: errorMessages[name] || 'Invalid' });
      return false;
    }
    return true;
  };

  if (!user) return null;

  return (
    <Form context={form}>
      <AdminView title={`Account`} className="staff-profile-update-view miq-container">
        <div className="d-grid grid-md-4 gap-2">
          <AdminView.Section title="Profile information" text="Update your profile information." className="span-md-3">
            <Form.Field label="First name" className="d-grid grid-md-5 mb-1">
              <Form.TextInput
                required
                name="first_name"
                onSave={saveAccount}
                isValid={validate}
                error={form.errors.first_name}
                placeholder="First name"
                className="span-md-3"
                minLength={2}
              />
            </Form.Field>

            <Form.Field label="Last name" className="d-grid grid-md-5 mb-1">
              <Form.TextInput
                required
                name="last_name"
                onSave={saveAccount}
                isValid={validate}
                error={form.errors.last_name}
                placeholder="Last name"
                className="span-md-3"
                minLength={2}
              />
            </Form.Field>

            <Form.Field label="Gender" className="d-grid grid-md-5 mb-1">
              <Form.SelectInput
                required
                name="gender"
                // value={user.gender}
                nullValue={{ label: 'Select gender' }}
                onChange={({ target }) => saveAccount(target)}
                className="span-md-3"
              >
                <option value="OTHER">Other</option>
                <option value="MALE">Male</option>
                <option value="FEMALE">Female</option>
              </Form.SelectInput>
            </Form.Field>

            {/* HRM */}
            {Object.keys(apps).includes('miq_hrm') && (
              <Form.Field label="Role" className="d-grid grid-md-5 mb-1">
                <Form.TextInput
                  required
                  name="position"
                  onSave={({ value }) =>
                    services
                      .patch(
                        'account/current/position/',
                        { position: value }
                        // { position: employee.position }
                      )
                      .catch((err) => {
                        form.handleError(err);
                      })
                  }
                  isValid={validate}
                  error={form.errors.position}
                  placeholder="Role"
                  className="span-md-3"
                  minLength={2}
                />
              </Form.Field>
            )}
          </AdminView.Section>

          <AdminView.Section
            title="Avatar"
            text="Update your profile picture. Images must be .png, .jpg or .gif format."
            actions={
              <ImgDeleteIconButton
                slug={user.img}
                onSuccess={() => updateUser({ img: null, img_data: null })}
                // onError={() => toast.error({ message: 'Something went wrong.' })}
                label=""
                className="btn-danger-3"
              />
            }
            className="avatar-section"
          >
            <div className="d-flex">{user.img ? <Avatar {...user.img_data} /> : <AvatarUploadButton />}</div>
          </AdminView.Section>
        </div>

        <AdminView.Section title="Email" text="Please contact support to change your email.">
          <p className="text-muted text-sm">{user.email}</p>
        </AdminView.Section>

        <AdminView.Section
          title="Password"
          text="Please contact support to change your password. Password must be at least 6 characters long."
        />
      </AdminView>
    </Form>
  );
};
