import { useContext } from 'react';
import PropTypes from 'prop-types';

import { Route } from 'react-router-dom';

import { SharedDataCtx } from '@miq/contexts';
import { getClassName } from '@miq/utils';
import { IconNavLink, Icons, Loading } from '@miq/components';
import { hasPerms } from '../utils';

/**
 ******************************** NAVLINKS
 */

const AdminSettingsNavLink = (props) => {
  const { path = '', label = 'Settings', Icon = Icons.Gear, ...rest } = props;
  return (
    <AdminNavLink
      Icon={Icon}
      label={label}
      {...props}
      to={`${path}settings/`}

      // requiredPerms={["miq.view_setting"]}
    />
  );
};

const AdminAccountNavLink = (props) => {
  const { path = '', user, Icon = Icons.Person, ...rest } = props;

  if (!user || !user.first_name) return null;

  return (
    <AdminNavLink
      Icon={Icon}
      label={user.first_name}
      {...rest}
      to={`${path}account/`}
      // requiredPerms={["miq.view_setting"]}
    />
  );
};

const AdminDashboardNavLink = (props) => {
  const { path = '', label = 'Dashboard', Icon = Icons.House, ...rest } = props;
  return <AdminNavLink {...rest} exact to={path} label={label} Icon={Icon} />;
};

/**
 * SIDEBAR
 */

export const AdminNavLink = ({ requiredPerms, ...props }) => {
  const ctx = useContext(SharedDataCtx);
  const { user, perms } = ctx;

  if (!user || !user.is_staff || (requiredPerms && !hasPerms(perms.perms, requiredPerms))) return null;

  return <IconNavLink {...props} className={getClassName(['miq-admin-link', props.className])} />;
};
AdminNavLink.propTypes = {
  requiredPerms: PropTypes.arrayOf(PropTypes.string),
};

export const AdminSidebar = ({ children, header, footer, ...props }) => {
  return (
    <nav {...props} className={getClassName(['miq-admin-aside', props.className])}>
      {header && <header className="miq-admin-aside-header">{header}</header>}

      <section className="miq-admin-aside-body">
        <div className="miq-admin-aside-body-content">{children}</div>

        {footer && <div className="miq-admin-aside-body-footer">{footer}</div>}
      </section>
    </nav>
  );
};

AdminSidebar.NavLink = AdminNavLink;
AdminSidebar.DashboardNavLink = AdminDashboardNavLink;
AdminSidebar.AccountNavLink = AdminAccountNavLink;
AdminSidebar.SettingsNavLink = AdminSettingsNavLink;

/**
 * MOBILE NAVIGATION
 */

export const AdminMobileNavbar = ({ children, ...props }) => {
  return (
    <nav {...props} className={getClassName(['miq-admin-mobile-nav', props.className])}>
      {children}
    </nav>
  );
};

export const AdminRoute = ({ requiredPerms, ...props }) => {
  const ctx = useContext(SharedDataCtx);
  const { user, perms } = ctx;

  if (!user || !user.is_staff || (requiredPerms && !hasPerms(perms.perms, requiredPerms)))
    return (
      <div>
        <h1>Not allowed...</h1>
      </div>
    );

  return <Route {...props} />;
};
