import React from 'react';
import { getClassName } from '@miq/utils';
import ViewHeader from './ViewHeader';

export default function ViewSection({ children, ...props }) {
  return (
    <div id={props.id} className={getClassName(['miq-view-section', props.className])}>
      <ViewHeader {...props} className="miq-view-header" />
      {children}
    </div>
  );
}
