import { getClassName } from '@miq/utils';
import ViewSection from './ViewSection';
import ViewHeader from './ViewHeader';
import { AdminNavLink } from '../Layout/components';

import './views.scss';

const ViewTabNavLinks = (props) => {
  const { urls, ...rest } = props;

  return (
    <div className={getClassName(['admin-view-tab-navlinks', 'd-flex', rest.className])}>
      {urls?.map((data) => (
        <AdminNavLink
          {...data}
          exact={data.exact}
          to={data.to}
          label={data.label}
          key={data.to}
          className={getClassName(['admin-view-tab-navlink', 'me-1', data.className])}
        />
      ))}
    </div>
  );
};

/**
 * AdminView
 * Usage:
 * ```js
 * <AdminView back="/shop/" title="View title" text="This is a view" actions={<Button>Hello</Button>}>
 *  <AdminView.Card></AdminView.Card>
 *  <div>This a message</div>
 *  <AdminView.Section></AdminView.Section>
 * </AdminView>
 * ```
 *
 * @param {Object} props
 * @param {string} props.back
 * @param {string} props.title
 * @param {string} props.text
 * @param {*} props.actions
 * @param {*} props.children
 * @param {string=} props.className
 * @param {string=} props.id
 * @augments {Component<Props, State>}
 */

export const AdminView = ({ children, ...props }) => {
  const { title, text, actions, back, ...rest } = props;

  return (
    <div {...rest} className="miq-view">
      <ViewHeader {...{ title, text, actions, back }} className="miq-view-header" />

      <div className={getClassName(['miq-view-main', props.className])}>{children}</div>
    </div>
  );
};

AdminView.Section = ViewSection;
AdminView.TabNavLinks = ViewTabNavLinks;
