import React from 'react';
import { Icons } from '@miq/components';
import { AdminNavLink } from '@miq/adminjs';

export default function ViewHeader(props) {
  const { back, title, text, actions } = props;
  const header = back || title || text || actions;

  if (!header) return null;

  return (
    <header className={props.className}>
      <div className="miq-view-header-inner d-flex">
        <div className="d-flex align-items-center">
          {back && <BackButton {...props} to={back} />}

          <div className="miq-view-title">
            {typeof title === 'string' ? <h2 className="text-md fw-normal">{title}</h2> : title}
          </div>
        </div>

        {actions && <section className="miq-view-actions">{actions}</section>}
      </div>

      {text && <div className="miq-view-text">{text}</div>}
    </header>
  );
}

const BackButton = (props) => {
  return <AdminNavLink title="Retourner derrirere" to={props.to} Icon={Icons.ArrowLeft} className="me-1" />;
};
