import React from 'react';
import { getClassName } from '@miq/utils';

import './card.scss';

/**
 * View Card
 * Usage:
 * ```js
 * <ViewCard title="View title" text="This is a title" actions={<Button>Hello</Button>} footer={<div>My footer</div>}>
 *  Click to show a message
 * </ViewCard>
 * ```
 *
 * @param {Object} props
 * @param {string} props.title
 * @param {string} props.text
 * @param {*} props.actions
 * @param {*} props.children
 * @param {*} props.footer
 * @param {string=} props.className
 * @param {string=} props.headerclassName
 * @param {string=} props.bodyclassName
 * @param {string=} props.footerclassName
 * @param {string=} props.id
 * @augments {Component<Props, State>}
 */

export function Card({ children, ...props }) {
  const { title, text, actions, footer } = props;
  const hasHeader = title || text || actions;

  return (
    <div id={props.id} className={getClassName(['view-card', props.className])}>
      {hasHeader && (
        <div className={getClassName(['view-card-header', props.headerclassName])}>
          <div className="d-flex align-items-center">
            <div className="view-card-title flex-1">{title}</div>

            {actions && <div className="view-card-actions">{actions}</div>}
          </div>

          {text && <div className="view-card-text">{text}</div>}
        </div>
      )}

      <div className={getClassName(['view-card-body', props.bodyclassName])}>{children}</div>

      {footer && <div className={getClassName(['view-card-footer', props.footerclassName])}>{footer}</div>}
    </div>
  );
}
