import { useState } from 'react';
import PropTypes from 'prop-types';

import Form from '@miq/form';
import { Button, ImgUploadButton, Img, ImgDeleteIconButton } from '@miq/components/';
import { getClassName, isRequired } from '@miq/utils/';
import { imgServices } from './utils';

const StaffCoverUpdateTab = ({ tab, data, ...props }) => {
  const btnProps = {
    slug: data?.slug,
    onCreate: props.onCreate,
    onUpdate: props.onUpdate,
    accept: props.accept || '.jpg,.jpeg,.png,.gif,.webp',
  };

  switch (tab) {
    case 'mobile':
      return (
        <div className="mobile">
          <ImgUploadButton {...btnProps} isMobile>
            <Img {...data} isMobile />
          </ImgUploadButton>
        </div>
      );

    default:
      return (
        <div className="desktop">
          <ImgUploadButton {...btnProps}>
            <Img {...data} />
          </ImgUploadButton>
          {!props.slug && <p className="mt-1">Add cover</p>}
        </div>
      );
  }
};

export const StaffCoverUpdateForm = (props) => {
  const [tab, setTab] = useState('desktop');
  const slug = props?.data?.slug || props.slug;

  return (
    <div className={getClassName(['staff-cover-update-form', props.className])}>
      {slug && (
        <div className="staff-cover-update-form-nav d-flex flex-row align-items-center justify-content-between mb-1">
          <div className="">
            <Button
              onClick={() => setTab('desktop')}
              className={getClassName(['me-1', tab === 'desktop' && 'btn-primary-3'])}
            >
              Desktop
            </Button>
            <Button onClick={() => setTab('mobile')} className={getClassName([tab === 'mobile' && 'btn-primary-3'])}>
              Mobile
            </Button>
          </div>
          <div>
            {slug && <ImgDeleteIconButton label="" className="btn-danger-3" slug={slug} onSuccess={props.onDelete} />}
          </div>
        </div>
      )}

      <StaffCoverUpdateTab {...props} tab={tab} slug={slug} />
    </div>
  );
};

export const ImageAltTextInput = ({ image, ...props }) => {
  const slug = image?.slug;

  if (!image || !slug) return null;

  const { form = isRequired('form'), toast, onSuccess, placeholder = 'Write alt text ...', required = true } = props;

  const save = ({ value }) => {
    if (!image.slug) return null;

    return imgServices
      .patch(slug, { alt_text: value })
      .then((data) => {
        if (onSuccess) return onSuccess(data);
        if (toast) return toast.success({ message: 'Image updated' });
      })
      .catch((err) => {
        if (form) return form.handleError(err);
      });
  };

  return (
    <Form.TextInput
      required={required}
      name="alt_text"
      onSave={save}
      error={form ? form.errors.alt_text : null}
      placeholder={placeholder}
      maxLength={99}
    />
  );
};
ImageAltTextInput.defaultProps = { form: { errors: {}, handleError: () => {} } };
ImageAltTextInput.propTypes = {
  image: PropTypes.shape({ slug: PropTypes.string }).isRequired,
  form: PropTypes.object,
  toast: PropTypes.object,
  onSuccess: PropTypes.func,
};
