import React from 'react';

import { getClassName } from '@miq/utils/';

/**
 * @typedef {Object} Props
 * @property {string} className
 * @property {string=} id
 *
 * @typedef {Object} InputProps
 * @property {string} name
 * @property {string|number} value
 * @property {string} placeholder
 * @property {function} onChange
 * @property {function} onFocus
 * @property {React.DOMAttributes<HTMLInputElement>.onBlur} onBlur
 * @property {React.DOMAttributes<HTMLInputElement>.onKeyPress} onKeyPress
 * @property {React.KeyboardEventHandler<HTMLInputElement>} onKeyUp
 * @property {function} onKeyDown
 * @property {boolean} required
 * @property {boolean} disabled
 * @property {boolean} autoFocus
 * @property {string} autoComplete
 *
 * @typedef {Props & InputProps} InnerProps
 */

//#region SearchInput

export const SearchInput = (props) => (
  <input
    name="q"
    placeholder="Search ..."
    {...props}
    type="search"
    className={getClassName(['miq-input', props.className, error && 'form-invalid'])}
  />
);

//#endregion SearchInput

//#region PhoneInput

/**
 * Phone input
 * Usage:
 * ```js
 * <PhoneInput onchange={(e)=>setState(e.target.value)} minLength={7} maxLength={16} pattern="[0-9]*"/>
 * ```
 *
 * @typedef {Object} PhoneProps
 * @property {string} pattern
 *
 * @typedef {InnerProps & PhoneProps} PhoneInputProps
 *
 * @param {PhoneInputProps} props
 *
 * @augments {Component<Props, State>}
 */

export const PhoneInput = (props) => (
  <input
    minLength={7}
    maxLength={16}
    pattern="[0-9]*"
    {...props}
    type="tel"
    className={getClassName(['miq-input', props.className, props.error && 'form-invalid'])}
    onKeyPress={(e) => !/[0-9]/.test(e.key) && e.preventDefault()}
  />
);
//#endregion PhoneInput

