import { lazy } from 'react';

import { Switch, Link } from 'react-router-dom';

import { AdminRoute } from '@miq/adminjs';
import { AdminView } from '../views';
import { addForwardSlash } from '@miq/utils/';

import './setting.scss';

const StaffSettingsGeneralView = lazy(() => import('./GeneralSettingsUpdateView'));
const StaffSiteIndexUpdateView = lazy(() => import('./IndexSettingsUpdateView'));

export default function SettingsLayout(props) {
  const { path } = props.match;
  const url = addForwardSlash(props.match.url);

  return (
    <Switch>
      <AdminRoute
        path={`${path}general/`}
        render={(args) => <StaffSettingsGeneralView {...args} back={url} />}
        // requiredPerms={['shop.view_product']}
      />
      <AdminRoute
        path={`${path}index/`}
        render={(args) => <StaffSiteIndexUpdateView {...args} back={url} />}
        // requiredPerms={['shop.view_product']}
      />
      <AdminRoute
        path={path}
        // requiredPerms={['miq.view_setting']}
      >
        <AdminView title="Settings" className="settings" back={'/staff/'}>
          <div className="d-grid grid-sm-3 grid-md-4">
            <SettingsLink to={`${path}general/`} title="General" text="Site settings and branding" />

            <SettingsLink to={`${path}index/`} title="Index" text="Homepage settings" />
          </div>
        </AdminView>
      </AdminRoute>
    </Switch>
  );
}
const SettingsLink = (props) => {
  const { title, text, ...rest } = props;
  return (
    <Link {...rest}>
      <div>
        <span className="text-md">{title}</span>
        <p className="text-muted text-sm pb-3">{text}</p>
      </div>
    </Link>
  );
};
