import PropTypes from 'prop-types';

import './components.scss';

import { getClassName } from './exports';

//#region PublishedStatusSpan

/**
 * Show a published/draft span. If short only shows the first letter(P/D)
 * Usage:
 * ```js
 * <PublishedStatusSpan is_published={true} short pill />
 * ```
 *
 * @param {Object} props
 * @param {bool} props.is_published
 * @param {bool} props.pill
 * @param {bool} props.short
 * @param {string=} props.className
 * @param {string=} props.id
 * @augments {Component<Props, State>}
 */

export const PublishedStatusSpan = ({ is_published, pill, short, className, id }) => {
  let label = is_published ? 'Published' : 'Draft';
  if (short) label = label[0];

  return (
    <span
      id={id}
      className={getClassName([
        'published-status-span',
        className,
        pill && 'pill',
        is_published ? 'published' : 'draft',
      ])}
    >
      {label}
    </span>
  );
};

PublishedStatusSpan.defaultProps = { is_published: false };

PublishedStatusSpan.propTypes = {
  is_published: PropTypes.bool,
  pill: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
  short: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
  id: PropTypes.string,
  className: PropTypes.string,
};

//#endregion PublishedStatusSpan
